<script>
import { ModelSelect } from "vue-search-select";
import Datepicker from "vuejs-datepicker";
import axios from "axios";
import Constantes from "../../store/constantes.js";
import _ from "lodash"
import Util from "../../tools/util";
import EventBus from "../../helpers/EventBus.js"
export default {
  name: "Registro",
  components: {
    Datepicker,
    ModelSelect
  },
  data() {
    return {
      app: { name: "appRentas", data: null },
      idtipoDocumento:0,
      tipoDocumento:'',
      numeroDocumento:'',
      nombres:'',
      telefono:'',
      celular:'',
      correo:'',
      usuario:'',
      correoNotificacion:'',
      direccion:'',
      registerSucess:false,
      flagCasillaElectronicaInicio:true,
      flagCasillaElectronica:true,
      idePersona:0,
      reg: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,
      mapListaDepartamento:null,
      itemDepartamento:14,
      mapListaProvincia:null,
      itemProvincia:1,
      mapListaDistrito:null,
      itemDistrito:1392,
      idTipoVia:0,
      listaTipoViaCtlgo:null,
      idTipoInterior:0,
      listaTipoInterior:null,
      idNumeroDireccion:0,
      listaNumeroDireccion:null,
      direccionVia:'',
      nroDireccion:'',
      refDireccion:'',
      itemVia:null,
      mapListaVia:null,
      manzana:'',
      lote:'',
      listaDirecciones:null,
      listaNumeroInterior:null,
      idNumeroInterior:'',
      zona:'',
      ubigeoInicial:1
    };
  },
  created() {
    this.cargarUsuario();
  },
  
  computed: {
    
  },
  filters: {
     
  },
  methods: {
    agregarDireccion(){
      /* if(this.listaDirecciones==null){
        this.listaDirecciones=[]
      } */
      let validarDirecciones=this.validarDirecciones();
      if(!validarDirecciones){
        return false;
      }
      this.listaDirecciones=[]
      let direccion={}
      direccion.departamento=this.obtenerNombreDepartamento()
      direccion.provincia=this.obtenerNombreProvincia()
      direccion.distrito=this.obtenerNombreDistrito()
      direccion.direccionTexto=this.obtenerDireccionTexto()
      
      direccion.referencia=this.refDireccion.toUpperCase()
      direccion.ideDireccion=-1
      direccion.ideTipoDirec=10376//FISCAL VIRTUAL
      direccion.direccion={}
      direccion.direccion.ideUbigeo=this.itemDistrito
      direccion.direccion.nomVia=this.itemDistrito==1392? this.obtenerNombreVia():this.direccionVia
      direccion.direccion.ideTipoVia=this.idTipoVia
      direccion.direccion.numDirec=this.itemDistrito==1392?this.idNumeroDireccion:this.nroDireccion
      direccion.direccion.numDireccSuf=""
      direccion.direccion.desCa=""
      direccion.direccion.nomManzan=this.manzana
      direccion.direccion.numLote=this.lote
      direccion.direccion.ideTipoInterior=this.itemDistrito==1392?null:this.idTipoInterior
      direccion.direccion.numInterior=this.itemDistrito==1392? this.idNumeroInterior:this.numeroInterior

      direccion.direccion.dscRefere=this.refDireccion.toUpperCase()
      direccion.direccion.dscZonaCatas=this.zona
      this.listaDirecciones.push(direccion)
      console.log('direccion texto=>' + direccion.direccionTexto)
      this.dataUser.direccion=direccion.direccionTexto
      console.log(this.dataUser.direccion)
      this.$bvModal.hide("registroDirecciones");
    },
    clearDataDirecciones(){

    },
    openModalCasillaElectronica(){
      this.$bvModal.show("modalCasillaElectronica");
    },
    llenarDepartamento(){
          axios.get(Constantes.API_PERSONA+'/ubigeo/1/0/0/0')    
                  .then(response=>{
                      let data=response.data.data;
                      this.mapListaDepartamento=[];
                      let i=0;
                      while(i<data.length){
                            var departamento={};
                            departamento.value=data[i].indDepartam;
                            departamento.text=data[i].desNombre;
                            this.mapListaDepartamento.push(departamento);
                            i++;
                        }
                  })
                  .catch(e=>console.log(e))
      },
    openModalDirecciones() {
      this.$bvModal.show("registroDirecciones");
      this.llenarDepartamento()
      this.llenarProvincia()
      this.llenarDistrito(),
      this.llenarTipoViaCtlgo(),
      this.llenarTipoInterior(),
      this.llenarVia(18)
    },
    llenarVia(dist){
        axios.get(Constantes.API_PERSONA+'/via/'+dist)    
                  .then(response=>{
                      let data=response.data.data;
                      this.mapListaVia=[];
                      let i=0;
                      while(i<data.length){
                            var via={};
                            via.value=data[i].call;
                            via.text=data[i].viaAbreviatura.trim() + ' ' + data[i].cala.trim() + ' ' + data[i].caln.trim();
                            via.tipoVia=data[i].tipoVia;
                            this.mapListaVia.push(via);
                            i++;
                        }
                  })
                  .catch(e=>console.log(e))
      },
    llenarTipoInterior(){
        axios.get(Constantes.rutaParametros+'/ctlmelecgo/147/0')    
                  .then(response=>{
                      this.listaTipoInterior=response.data.data;
                  })
                  .catch(e=>console.log(e))
      },
    llenarTipoViaCtlgo(){
        axios.get(Constantes.rutaParametros+'/ctlmelecgo/29/0')    
                  .then(response=>{
                      this.listaTipoViaCtlgo=response.data.data;
                  })
                  .catch(e=>console.log(e))
      },
    llenarDistrito(){
        console.log(Constantes.API_PERSONA+'/ubigeo/1/'+this.itemDepartamento+'/'+this.itemProvincia+'/0')
        axios.get(Constantes.API_PERSONA+'/ubigeo/1/'+this.itemDepartamento+'/'+this.itemProvincia+'/0')    
                  .then(response=>{
                      let data=response.data.data;
                      this.mapListaDistrito=[]
                      console.log(data)
                      let i=0;
                      while(i<data.length){
                            var distrito={};
                            distrito.value=data[i].ideUbigeo;
                            distrito.text=data[i].desNombre;
                            this.mapListaDistrito.push(distrito);
                            i++;
                        }
                  })
                  .catch(e=>console.log(e))
          
      },
    llenarProvincia(){
        axios.get(Constantes.API_PERSONA+'/ubigeo/1/'+this.itemDepartamento+'/0/0')    
                  .then(response=>{
                      let data=response.data.data;
                      this.mapListaProvincia=[]
                      let i=0;
                      while(i<data.length){
                            var provincia={};
                            provincia.value=data[i].indProvincia;
                            provincia.text=data[i].desNombre;
                            this.mapListaProvincia.push(provincia);
                            i++;
                        }
                        if(this.ubigeoInicial==1){
                          this.ubigeoInicial=0;
                        }else{
                          this.llenarDistrito()
                        }
                  })
                  .catch(e=>console.log(e))
      },
    cargarUsuario(){
        axios.get(Constantes.rutaTramite+'/detalleUsuarioPlataforma/'+localStorage.getItem('idUsuarioLogueadoTramite'))    
                  .then(response=>{
                      let data=response.data.data;
                      this.tipoDocumento=data[0].persona.tipoDocumento.desNombre;
                      this.numeroDocumento=data[0].persona.penumdoc?data[0].persona.penumdoc.trim():data[0].persona.penumdoc;
                      this.nombres=data[0].persona.desNombreCompleto?data[0].persona.desNombreCompleto.trim():data[0].persona.desNombreCompleto;
                      this.telefono=data[0].telefono?data[0].telefono.trim():data[0].telefono;
                      this.celular=data[0].celular?data[0].celular.trim():data[0].celular;
                      this.correo=data[0].usuario?data[0].usuario.trim():data[0].usuario;
                      this.usuario=data[0].usuario?data[0].usuario.trim():data[0].usuario;
                      this.correoNotificacion=data[0].correoNotificacion?data[0].correoNotificacion.trim():data[0].correoNotificacion;
                      this.flagCasillaElectronica=data[0].flagCasillaElectronica==1?true:false;
                      this.flagCasillaElectronicaInicio=data[0].flagCasillaElectronica==1?true:false;
                      this.idePersona=data[0].persona.idePersona
                  })
                  .catch(e=>console.log(e))
    },
    validaCorreoFormato(correo) {
      return correo == "" ? false : this.reg.test(correo) ? true : false;
    }
    ,validaFormulario(){
        let validado=true;
        let mensajeError='';
        let coma='';
        if(this.telefono==null || this.telefono=='')
        {
            validado=false;
            mensajeError='teléfono'
            coma=','
        }
        if(this.correo==null||this.correo==''){
            validado=false;
            mensajeError=mensajeError+coma+' correo electrónico'
            coma=','
        }else if (!this.validaCorreoFormato(this.correo)) {
            validado=false;
            mensajeError = mensajeError + coma + " correo electrónico inválido"
            coma = ", "
        }
        if(this.correoNotificacion==null||this.correoNotificacion==''){
            validado=false;
            mensajeError=mensajeError+coma+' correo de notificación'
            coma=','
        }else if (!this.validaCorreoFormato(this.correoNotificacion)) {
            validado=false;
            mensajeError = mensajeError + coma + " correo notificación inválido"
            coma = ", "
        }
        if(!validado){
            this.$swal({
            icon: "info",
            title: "Aviso",
            text: "Debe completar los campos " + mensajeError
            });
        }
        return validado;
    }
    ,editarUsuario(){
        let formularioValido=this.validaFormulario();
        if(!formularioValido){
            return false;
        }
        let usuarioPtd = {};
                        usuarioPtd.usuario = this.usuario;
                        usuarioPtd.usuarioNuevo=this.correo;
                        usuarioPtd.correoNotificacion=this.correoNotificacion;
                        usuarioPtd.celular=this.celular;
                        usuarioPtd.telefono=this.telefono;
                        usuarioPtd.persona={}
                        usuarioPtd.persona.penumdoc=this.numeroDocumento
                        usuarioPtd.persona.idePersona=this.idePersona
                        usuarioPtd.activado=1
                        usuarioPtd.flagCasillaElectronica=this.flagCasillaElectronica?1:0
        axios.post(Constantes.rutaTramite+'/ptdusuario-editar',usuarioPtd)    
                  .then(response=>{
                        if(response.data.success){
                            this.registerSucess =true            
                        }else{
                            this.$swal({
                                icon: "info",
                                title: "Aviso",
                                text: "Sucedió un error al editar: " + response.data.message
                            });             
                        }
                        
                  })
                  .catch(e=>console.log(e))
    }
  }
};
</script>


